export default {
  props: {
    articleList: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data () {
    return {
      leftBox: [],
      rightBox: []
    }
  },
  watch: {
    articleList: {
      deep: true,
      handler: function () {
        this.filterArr()
      }
    }
  },
  methods: {
    // 将列表分割成左右两边的瀑布流列表
    filterArr() {
      let list= this.articleList,
        that= this
      that.leftBox= []
      that.rightBox= [] 
      
      console.log('变化了', list)
        
      if(list.length > 0) {
        list.map((item, index) => {
          if(index % 2 === 0) {
            that.leftBox.push(item)
          } else {
            that.rightBox.push(item)
          }
        })  
      }
    },
    
    // 文章点赞
    likeArticle() {
      console.log('文章点赞')
      let that= this
      let url = this.$api.articleLike
      if (this.detail.isLike) {
        url = this.$api.articleunLike
      }
      let params= {
        url: url,
        data: {
          article_id: this.article_id
        }
      }
      that.$httpRequest(params).then((res) => {
        let isLike= that.detail.isLike,
          count= that.detail.articlelike_count
        that.detail.articlelike_count= isLike? count - 1: count + 1
        that.detail.isLike= !isLike
      })
    },
    
    goDetail(id) {
      uni.navigateTo({
        url: '../index/article/detail?article_id=' + id
      })
    },
    likeArticle() {
      console.log('点赞')
    }
  }
}
